import './style.scss';
import FeatureIcon from "./icon";

const FeatureItem = props => {
    return (
        <a className="feature" href={props.href || '#'} target={'_blank'} rel={'noreferrer noopener'}>
            <div className="feature__wrapper mb-4 mb-lg-0">
                <div className={`feature__icon`}>
                    <FeatureIcon name={props.iconName}/>
                </div>
                <h3 className={`feature__title`}>
                    {props.title}
                </h3>
                <p className="feature__desc">
                    {props.children || props.description || props.desc}
                </p>
            </div>
        </a>
    )
}

export default FeatureItem;